#translations_card .card-footer {
  position: sticky;
  bottom: 0;
  background-color: #FFF;
}
#translations_card #translations_search {
  min-height: 65px;
  padding: 10px 15px;
  font-size: 1.5rem;
  border: 0;
  background-color: #fff;
  box-shadow: 0 0.25rem 1rem 0.25rem rgba(0, 0, 0, 0.075) !important;
  border-radius: 8px;
}
#translations_card #translations_search:focus {
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
}
#translations_card .hide-field {
  transform: scale(0);
  position: absolute;
  z-index: -1;
}